import { Container ,Box, List, ListItem, Typography, TextField, Button, Link } from "@mui/material"


const Login=()=>{
    return(
        <>
        <Box sx={{backgroundColor:'white.cream',width:'100vw',height:'calc(100vh - 70px)',display:"flex",justifyContent:"center",alignItems:"center"}}>
            <Container sx={{backgroundColor:'white.main',width:{xs:300,sm:400},display:"flex",justifyContent:"center",alignItems:"center",borderRadius:8,flexDirection:"column",boxShadow:5,padding:"2rem"}}>  
                    <Typography variant="h4" color="primary.main" sx={{margin:2}}>LOGIN</Typography>
                    <TextField label="Email" sx={{width:"100%",marginBottom:"1.5rem"}} type="email"></TextField>
                    <TextField label="Password" sx={{width:"100%",marginBottom:"1.5rem"}} type="password"></TextField>
                    <Button variant="contained" sx={{backgroundColor:"secondary.main",marginBottom:"1.5rem"}}>login</Button>
                    <Link>Don't have an account? Sign Up</Link>
            </Container>
        </Box>
        </>
    )
}

export default Login;